
"use client";

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm, type SubmitHandler } from "react-hook-form";
import { z } from "zod";
import { useState, useTransition, useEffect } from "react";
import Image from "next/image";

import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Separator } from "@/components/ui/separator";
import { Label } from "@/components/ui/label";
import { useToast } from "@/hooks/use-toast";
import { getAILayoutSuggestion } from "@/app/actions";
import type { SuggestLayoutOutput } from "@/ai/flows/suggest-layout";
import { Car, Lightbulb, Sparkles, Loader2, Settings2, DollarSign, Tag, FileText, Wrench, Fuel, ShieldCheck, CheckSquare, ListChecks, Link2, Download } from "lucide-react";

const vehicleFormSchema = z.object({
  // Section 1: Vehicle Details
  marca: z.string().min(1, "La marca es obligatoria"),
  modelo: z.string().min(1, "El modelo es obligatorio"),
  version: z.string().min(1, "La versión es obligatoria"),

  // Section 2: Pricing
  price_10000_36: z.string().optional(),
  price_10000_48: z.string().optional(),
  price_15000_36: z.string().optional(),
  price_15000_48: z.string().optional(),
  price_20000_36: z.string().optional(),
  price_20000_48: z.string().optional(),
  price_25000_36: z.string().optional(),
  price_25000_48: z.string().optional(),
  price_30000_36: z.string().optional(),
  price_30000_48: z.string().optional(),
  price_35000_36: z.string().optional(), // Not in PDF, but keep for consistency
  price_35000_48: z.string().optional(), // Not in PDF, but keep for consistency
  price_40000_36: z.string().optional(), // Not in PDF, but keep for consistency
  price_40000_48: z.string().optional(), // Not in PDF, but keep for consistency
  // The PDF shows 5 pricing tiers. We have 7 in the form.
  // For PDF, we'll use 10k, 15k, 20k, 25k, 30k.

  entrada_36: z.string().optional(), // Not in PDF template
  entrada_48: z.string().optional(), // Not in PDF template
  moves_checkbox: z.boolean().default(false), // Not in PDF template

  // Section 3: General Selection & Labels
  main_selection: z.string().optional(), // Not in PDF template
  label_O: z.boolean().default(false), // For ECO label in PDF if needed
  label_ECO: z.boolean().default(false), // For ECO label in PDF
  label_B: z.boolean().default(false),
  label_C: z.boolean().default(false),

  // Section 4: Featured Elements
  elementos_destacados: z.string().optional().describe("Lista de elementos destacados, uno por línea"),

  // Section 5: Technical Specifications
  motorizacion: z.string().optional().describe("Ej: DIÉSEL, GASOLINA, HÍBRIDO"), // MOTORIZACIÓN
  transmision: z.string().optional().describe("Ej: AUTOMÁTICO, MANUAL"), // TRANSMISIÓN
  potencia_kw: z.string().optional(), // POTENCIA (kW)
  potencia_cv: z.string().optional(), // POTENCIA (CV)
  dimensiones_ancho: z.string().optional(), // ANCHO (cm)
  dimensiones_alto: z.string().optional(), // ALTO (cm)
  dimensiones_largo: z.string().optional(), // LARGO (cm)
  cilindrada: z.string().optional(), // CILINDRADA (No. of cylinders or cc, PDF shows "4")
  deposito_l: z.string().optional(), // DEPÓSITO (L)
  emisiones_gkm: z.string().optional(), // EMISIONES (g/km)
  consumo_wltp_l100km: z.string().optional(), // CONSUMO WLTP (l/100km)

  // Section 6: Insurance Options 1 (CONDICIONES in PDF)
  seguro_todo_riesgo: z.boolean().default(false).describe("Indica si es seguro todo riesgo con franquicia"),
  franquicia: z.string().optional().describe("Importe de la franquicia, ej: 300"), // For "Seguro todo riesgo con franquicia de X €"
  coche_sustitucion_1: z.boolean().default(false).describe("Indica si tiene vehículo de sustitución"), // For "Sin vehículo de sustitución" or "Con vehículo..."
  neumaticos: z.string().optional().describe("Número de neumáticos, ej: 4-8-12"), // Nº de neumáticos

  // Section 7: Insurance Options 2 (OPCIONAL in PDF)
  seguro_sin_franquicia: z.boolean().default(false).describe("Indica si es seguro todo riesgo sin franquicia opcional"),
  precio_mes_sin_franquicia: z.string().optional().describe("Coste adicional mensual, ej: 25"), // For "+ X €"
  coche_sustitucion_2: z.boolean().default(false).describe("Indica si el vehículo de sustitución opcional está activado"),
  precio_mes_sustitucion_2: z.string().optional().describe("Coste adicional mensual, ej: 20"), // For "+ X €"
});

export type VehicleFormValues = z.infer<typeof vehicleFormSchema>;

const pricingTiers = [10000, 15000, 20000, 25000, 30000, 35000, 40000];
// PDF shows 5 tiers: 10k, 15k, 20k, 25k, 30k
const pdfPricingTiers = [10000, 15000, 20000, 25000, 30000];


export function PrintForgeForm() {
  const { toast } = useToast();
  const [isPendingAI, startAITransition] = useTransition();
  const [isPendingSubmit, startSubmitTransition] = useTransition();
  const [isGeneratingPdf, setIsGeneratingPdf] = useState(false);
  const [aiSuggestion, setAISuggestion] = useState<SuggestLayoutOutput | null>(null);
  
  const [aiContentLength, setAiContentLength] = useState<string>("medium");
  const [aiBrandingElements, setAiBrandingElements] = useState<string>("");

  const form = useForm<VehicleFormValues>({
    resolver: zodResolver(vehicleFormSchema),
    defaultValues: {
      marca: "BMW",
      modelo: "118D",
      version: "Pack M",
      moves_checkbox: false,
      label_O: false,
      label_ECO: true, // Default to ECO for PDF example
      label_B: false,
      label_C: false,
      seguro_todo_riesgo: true, // Default to true for "CONDICIONES" example
      franquicia: "300", // Default for "CONDICIONES" example
      coche_sustitucion_1: false, // Default for "Sin vehículo de sustitución"
      neumaticos: "4-8-12", // Default for "CONDICIONES" example
      seguro_sin_franquicia: true, // Default for "OPCIONAL" example
      precio_mes_sin_franquicia: "25", // Default for "OPCIONAL" example
      coche_sustitucion_2: true, // Default for "OPCIONAL" example
      precio_mes_sustitucion_2: "20", // Default for "OPCIONAL" example
      elementos_destacados: `Equipamiento exterior del Paquete M Sport
Llantas de aleación ligera
Faros led y luces traseras led
Calefacción de los asientos delanteros
Parking Assistant
BMW Live Cockpit Plus con BMW Curved Display
BMW Operating System 9 con navegación
Climatizador
Volante deportivo
Carga inalámbrica para dispositivos móviles`,
      // Default technical specs for PDF example
      potencia_kw: "110",
      potencia_cv: "150",
      transmision: "AUTOMÁTICO",
      motorizacion: "DIÉSEL",
      dimensiones_ancho: "1800",
      dimensiones_alto: "1459",
      dimensiones_largo: "4361",
      cilindrada: "4", // As per PDF example (could be # cylinders)
      deposito_l: "49",
      emisiones_gkm: "124",
      consumo_wltp_l100km: "4,7",
      // Default pricing for PDF example (first 5 tiers)
      price_10000_36: "565", price_10000_48: "515",
      price_15000_36: "580", price_15000_48: "530",
      price_20000_36: "595", price_20000_48: "545",
      price_25000_36: "610", price_25000_48: "560",
      price_30000_36: "625", price_30000_48: "575",
    },
  });

  const handleSuggestLayout = async () => {
    if (!aiContentLength || !aiBrandingElements) {
      toast({
        title: "Información Faltante para IA",
        description: "Por favor, proporciona la longitud del contenido y los elementos de marca para las sugerencias de IA.",
        variant: "destructive",
      });
      return;
    }
    
    startAITransition(async () => {
      try {
        setAISuggestion(null);
        const suggestion = await getAILayoutSuggestion({ contentLength: aiContentLength, brandingElements: aiBrandingElements });
        setAISuggestion(suggestion);
        toast({
          title: "¡Sugerencia de IA Lista!",
          description: "Se ha generado la sugerencia de diseño.",
        });
      } catch (error) {
        console.error(error);
        toast({
          title: "Falló la Sugerencia de IA",
          description: "No se pudo obtener la sugerencia de diseño. Por favor, inténtalo de nuevo.",
          variant: "destructive",
        });
      }
    });
  };

  const onSubmit: SubmitHandler<VehicleFormValues> = (data) => {
    startSubmitTransition(async () => {
      setIsGeneratingPdf(true);
      toast({
        title: "Generando PDF...",
        description: "Por favor, espera mientras preparamos tu documento.",
      });
      try {
        const response = await fetch('/api/generate-pdf', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(data),
        });

        if (!response.ok) {
          let apiErrorMsg = `Error del servidor: ${response.status}`;
          try {
            // API is expected to return { message: string, error: string } on failure
            const errorData = await response.json();
            if (errorData && errorData.error && typeof errorData.error === 'string') {
              apiErrorMsg = errorData.error; // Use the detailed error message from server
            } else if (errorData && errorData.message && typeof errorData.message === 'string') {
              apiErrorMsg = errorData.message; // Fallback to the general message
            }
          } catch (e) {
            // If response.json() fails or structure is unexpected
            apiErrorMsg = `Error al procesar respuesta del servidor (${response.status}). Inténtalo de nuevo.`;
          }
          throw new Error(apiErrorMsg);
        }

        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        const marca = data.marca || "vehiculo";
        const modelo = data.modelo || "";
        a.download = `ficha_${marca.replace(/\s+/g, '_')}_${modelo.replace(/\s+/g, '_')}.pdf`;
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        a.remove();

        toast({
          title: "¡PDF Generado!",
          description: "Tu ficha de vehículo se ha descargado.",
        });
      } catch (error: any) {
        console.error("Error al generar PDF:", error);
        toast({
          title: "Error al Generar PDF",
          description: error.message || "No se pudo generar el PDF. Por favor, inténtalo de nuevo.",
          variant: "destructive",
        });
      } finally {
        setIsGeneratingPdf(false);
      }
    });
  };
  
  // Hydration fix for initial year in footer
  const [currentYear, setCurrentYear] = useState<number | null>(null);
  useEffect(() => {
    setCurrentYear(new Date().getFullYear());
  }, []);


  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-8 items-start">
      <Card className="md:col-span-2 shadow-xl">
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-2xl">
            <Car className="h-7 w-7 text-primary" />
            Configura Tu Vehículo
          </CardTitle>
          <CardDescription>
            Completa los detalles a continuación para la configuración del vehículo y generar la ficha PDF.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
              
              <Card className="p-4 sm:p-6">
                <CardHeader className="p-0 pb-4">
                  <CardTitle className="text-xl flex items-center gap-2"><Tag className="h-5 w-5" />Detalles del Vehículo</CardTitle>
                </CardHeader>
                <CardContent className="p-0 space-y-4">
                  <FormField control={form.control} name="marca" render={({ field }) => (
                    <FormItem>
                      <FormLabel>Marca</FormLabel>
                      <FormControl><Input placeholder="ej., BMW" {...field} /></FormControl>
                      <FormMessage />
                    </FormItem>
                  )} />
                  <FormField control={form.control} name="modelo" render={({ field }) => (
                    <FormItem>
                      <FormLabel>Modelo</FormLabel>
                      <FormControl><Input placeholder="ej., 118D" {...field} /></FormControl>
                      <FormMessage />
                    </FormItem>
                  )} />
                  <FormField control={form.control} name="version" render={({ field }) => (
                    <FormItem>
                      <FormLabel>Versión/Paquete</FormLabel>
                      <FormControl><Input placeholder="ej., Pack M" {...field} /></FormControl>
                      <FormMessage />
                    </FormItem>
                  )} />
                </CardContent>
              </Card>

              <Card className="p-4 sm:p-6">
                <CardHeader className="p-0 pb-4">
                   <CardTitle className="text-xl flex items-center gap-2"><DollarSign className="h-5 w-5" />Precios de Renting (para PDF - primeros 5 tramos)</CardTitle>
                </CardHeader>
                <CardContent className="p-0 space-y-4">
                    <div className="grid grid-cols-3 gap-x-4 gap-y-2 items-center mb-2">
                        <FormLabel className="font-semibold">Km/año</FormLabel>
                        <FormLabel className="font-semibold text-center">36 Meses (€)</FormLabel>
                        <FormLabel className="font-semibold text-center">48 Meses (€)</FormLabel>
                    </div>
                    {pdfPricingTiers.map(tier => (
                        <div key={tier} className="grid grid-cols-3 gap-x-4 gap-y-2 items-center">
                            <FormLabel>{tier.toLocaleString('es-ES')}</FormLabel>
                            <FormField control={form.control} name={`price_${tier}_36` as keyof VehicleFormValues} render={({ field }) => (
                                <FormItem>
                                    <FormControl><Input type="number" placeholder="Precio" {...field} /></FormControl>
                                    <FormMessage />
                                </FormItem>
                            )} />
                            <FormField control={form.control} name={`price_${tier}_48` as keyof VehicleFormValues} render={({ field }) => (
                                <FormItem>
                                    <FormControl><Input type="number" placeholder="Precio" {...field} /></FormControl>
                                    <FormMessage />
                                </FormItem>
                            )} />
                        </div>
                    ))}
                    <p className="text-xs text-muted-foreground pt-2">Los precios para 35.000 y 40.000 km/año se pueden introducir pero no se mostrarán en este diseño de PDF específico.</p>
                     <FormField control={form.control} name="moves_checkbox" render={({ field }) => (
                        <FormItem className="flex flex-row items-center space-x-3 space-y-0 pt-2">
                            <FormControl><Checkbox checked={field.value} onCheckedChange={field.onChange} /></FormControl>
                            <FormLabel className="font-normal">Plan Moves (No en PDF)</FormLabel>
                        </FormItem>
                    )} />
                </CardContent>
              </Card>
              
              <Card className="p-4 sm:p-6">
                <CardHeader className="p-0 pb-4">
                  <CardTitle className="text-xl flex items-center gap-2"><ListChecks className="h-5 w-5" />Selecciones y Características</CardTitle>
                </CardHeader>
                <CardContent className="p-0 space-y-6">
                   <FormField control={form.control} name="main_selection" render={({ field }) => (
                      <FormItem>
                          <FormLabel>Selección General (No en PDF)</FormLabel>
                          <Select onValueChange={field.onChange} defaultValue={field.value}>
                              <FormControl><SelectTrigger><SelectValue placeholder="Selecciona una opción" /></SelectTrigger></FormControl>
                              <SelectContent>
                                  <SelectItem value="option1">Opción 1</SelectItem>
                                  <SelectItem value="option2">Opción 2</SelectItem>
                                  <SelectItem value="option3">Opción 3</SelectItem>
                              </SelectContent>
                          </Select>
                          <FormMessage />
                      </FormItem>
                  )} />
                  <div>
                    <FormLabel className="mb-2 block">Etiquetas Ambientales (Para el logo ECO en PDF)</FormLabel>
                    <div className="flex flex-wrap gap-4">
                      <FormField control={form.control} name="label_ECO" render={({ field }) => (
                        <FormItem className="flex flex-row items-center space-x-2 space-y-0">
                          <FormControl><Checkbox checked={field.value} onCheckedChange={field.onChange} /></FormControl>
                          <FormLabel className="font-normal">ECO (usada en PDF)</FormLabel>
                        </FormItem>
                      )} />
                      {(['O', 'B', 'C'] as const).map(label => (
                        <FormField key={label} control={form.control} name={`label_${label}` as keyof VehicleFormValues} render={({ field }) => (
                          <FormItem className="flex flex-row items-center space-x-2 space-y-0">
                            <FormControl><Checkbox checked={field.value} onCheckedChange={field.onChange} /></FormControl>
                            <FormLabel className="font-normal">{label} (No en PDF)</FormLabel>
                          </FormItem>
                        )} />
                      ))}
                    </div>
                  </div>
                  <Button type="button" variant="outline" disabled>
                    <Link2 className="mr-2 h-4 w-4" /> Enlace a Formulario Externo (No funcional)
                  </Button>
                  <FormField control={form.control} name="elementos_destacados" render={({ field }) => (
                      <FormItem>
                          <FormLabel>Equipamiento Destacado (para PDF, uno por línea)</FormLabel>
                          <FormControl><Textarea placeholder="Lista los elementos destacados, uno por línea..." className="min-h-[120px]" {...field} /></FormControl>
                          <FormDescription>Cada línea se mostrará como un punto en el PDF.</FormDescription>
                          <FormMessage />
                      </FormItem>
                  )} />
                </CardContent>
              </Card>

              <Card className="p-4 sm:p-6">
                <CardHeader className="p-0 pb-4">
                  <CardTitle className="text-xl flex items-center gap-2"><Wrench className="h-5 w-5" />Especificaciones Técnicas (para PDF)</CardTitle>
                </CardHeader>
                <CardContent className="p-0 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
                  <FormField control={form.control} name="potencia_kw" render={({ field }) => (
                    <FormItem><FormLabel>Potencia (kW)</FormLabel><FormControl><Input type="text" placeholder="ej. 110" {...field} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="potencia_cv" render={({ field }) => (
                    <FormItem><FormLabel>Potencia (CV)</FormLabel><FormControl><Input type="text" placeholder="ej. 150" {...field} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="transmision" render={({ field }) => (
                    <FormItem><FormLabel>Transmisión</FormLabel>
                     <Select onValueChange={field.onChange} defaultValue={field.value}>
                        <FormControl><SelectTrigger><SelectValue placeholder="Selecciona transmisión" /></SelectTrigger></FormControl>
                        <SelectContent>
                          <SelectItem value="AUTOMÁTICO">AUTOMÁTICO</SelectItem>
                          <SelectItem value="MANUAL">MANUAL</SelectItem>
                        </SelectContent>
                      </Select>
                    <FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="motorizacion" render={({ field }) => (
                    <FormItem>
                      <FormLabel>Motorización</FormLabel>
                      <Select onValueChange={field.onChange} defaultValue={field.value}>
                        <FormControl><SelectTrigger><SelectValue placeholder="Selecciona tipo de motor" /></SelectTrigger></FormControl>
                        <SelectContent>
                          <SelectItem value="DIÉSEL">DIÉSEL</SelectItem>
                          <SelectItem value="GASOLINA">GASOLINA</SelectItem>
                          <SelectItem value="HÍBRIDO">HÍBRIDO</SelectItem>
                          <SelectItem value="ELÉCTRICO">ELÉCTRICO</SelectItem>
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )} />
                  <FormField control={form.control} name="dimensiones_ancho" render={({ field }) => (
                    <FormItem><FormLabel>Ancho (cm)</FormLabel><FormControl><Input type="text" placeholder="ej. 1800" {...field} onChange={e => field.onChange(e.target.value.replace(/[^0-9]/g, ''))} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="dimensiones_alto" render={({ field }) => (
                    <FormItem><FormLabel>Alto (cm)</FormLabel><FormControl><Input type="text" placeholder="ej. 1459" {...field} onChange={e => field.onChange(e.target.value.replace(/[^0-9]/g, ''))} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="dimensiones_largo" render={({ field }) => (
                    <FormItem><FormLabel>Largo (cm)</FormLabel><FormControl><Input type="text" placeholder="ej. 4361" {...field} onChange={e => field.onChange(e.target.value.replace(/[^0-9]/g, ''))} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="cilindrada" render={({ field }) => ( // Assuming this is # of cylinders as per PDF
                    <FormItem><FormLabel>Cilindrada / Nº Cilindros</FormLabel><FormControl><Input type="text" placeholder="ej. 4" {...field} /></FormControl><FormMessage /></FormItem>
                  )} />
                   <FormField control={form.control} name="deposito_l" render={({ field }) => (
                    <FormItem><FormLabel>Depósito (L)</FormLabel><FormControl><Input type="text" placeholder="ej. 49" {...field} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="emisiones_gkm" render={({ field }) => (
                    <FormItem><FormLabel>Emisiones (g/km)</FormLabel><FormControl><Input type="text" placeholder="ej. 124" {...field} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="consumo_wltp_l100km" render={({ field }) => (
                    <FormItem><FormLabel>Consumo WLTP (L/100km)</FormLabel><FormControl><Input type="text" placeholder="ej. 4,7" {...field} /></FormControl><FormMessage /></FormItem>
                  )} />
                </CardContent>
              </Card>
              
              <Card className="p-4 sm:p-6">
                <CardHeader className="p-0 pb-4">
                  <CardTitle className="text-xl flex items-center gap-2"><ShieldCheck className="h-5 w-5" />Condiciones y Opcionales (para PDF)</CardTitle>
                </CardHeader>
                <CardContent className="p-0 space-y-6">
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                    <div className="space-y-4 p-4 border rounded-md">
                      <h4 className="font-medium">Condiciones Base</h4>
                      <FormField control={form.control} name="seguro_todo_riesgo" render={({ field }) => (
                        <FormItem className="flex flex-row items-center space-x-3 space-y-0">
                          <FormControl><Checkbox checked={field.value} onCheckedChange={field.onChange} /></FormControl>
                          <FormLabel className="font-normal">Seguro todo riesgo con franquicia</FormLabel><FormMessage />
                        </FormItem>
                      )} />
                      <FormField control={form.control} name="franquicia" render={({ field }) => (
                        <FormItem><FormLabel>Importe Franquicia (€)</FormLabel><FormControl><Input type="text" placeholder="ej. 300" {...field} /></FormControl><FormMessage /></FormItem>
                      )} />
                      <FormField control={form.control} name="coche_sustitucion_1" render={({ field }) => ( // Inverted logic for PDF: checkbox means "HAS car"
                        <FormItem className="flex flex-row items-center space-x-3 space-y-0">
                           <FormControl><Checkbox checked={field.value} onCheckedChange={field.onChange}/></FormControl>
                           <FormLabel className="font-normal">Incluye vehículo de sustitución</FormLabel>
                           <FormDescription>(Si no se marca, en PDF: "Sin vehículo de sustitución")</FormDescription>
                           <FormMessage />
                        </FormItem>
                      )} />
                      <FormField control={form.control} name="neumaticos" render={({ field }) => (
                        <FormItem><FormLabel>Nº de Neumáticos</FormLabel><FormControl><Input placeholder="ej. 4-8-12" {...field} /></FormControl><FormMessage /></FormItem>
                      )} />
                    </div>

                    <div className="space-y-4 p-4 border rounded-md">
                      <h4 className="font-medium">Opcional Adicional</h4>
                       <FormField control={form.control} name="seguro_sin_franquicia" render={({ field }) => (
                        <FormItem className="flex flex-row items-center space-x-3 space-y-0">
                          <FormControl><Checkbox checked={field.value} onCheckedChange={field.onChange} /></FormControl>
                          <FormLabel className="font-normal">Seguro todo riesgo sin franquicia (Opcional)</FormLabel><FormMessage />
                        </FormItem>
                      )} />
                      <FormField control={form.control} name="precio_mes_sin_franquicia" render={({ field }) => (
                        <FormItem><FormLabel>Coste Adicional / Mes (€)</FormLabel><FormControl><Input type="text" placeholder="ej. 25" {...field} /></FormControl><FormMessage /></FormItem>
                      )} />
                      <FormField control={form.control} name="coche_sustitucion_2" render={({ field }) => (
                        <FormItem className="flex flex-row items-center space-x-3 space-y-0">
                           <FormControl><Checkbox checked={field.value} onCheckedChange={field.onChange} /></FormControl>
                           <FormLabel className="font-normal">Vehículo de Sustitución (Opcional)</FormLabel><FormMessage />
                        </FormItem>
                      )} />
                      <FormField control={form.control} name="precio_mes_sustitucion_2" render={({ field }) => (
                        <FormItem><FormLabel>Coste Adicional / Mes (€)</FormLabel><FormControl><Input type="text" placeholder="ej. 20" {...field} /></FormControl><FormMessage /></FormItem>
                      )} />
                    </div>
                  </div>
                </CardContent>
              </Card>

              <Separator className="my-8" />

              <div className="flex flex-col sm:flex-row justify-start gap-4">
                <Button type="submit" size="lg" disabled={isPendingSubmit || isPendingAI || isGeneratingPdf}>
                  {isPendingSubmit || isGeneratingPdf ? (
                    <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                  ) : (
                    <Download className="mr-2 h-5 w-5" />
                  )}
                  {isGeneratingPdf ? "Generando PDF..." : "Generar y Descargar PDF"}
                </Button>
              </div>
            </form>
          </Form>
        </CardContent>
      </Card>

      <div className="space-y-6 md:col-span-1">
        <Card className="shadow-xl">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-xl">
              <Lightbulb className="h-6 w-6 text-primary" />
              Sugerencia de Diseño IA (Opcional)
            </CardTitle>
            <CardDescription>Obtén un diseño óptimo basado en tu entrada para la generación de PDF.</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <Label htmlFor="aiContentLength" className="text-sm font-medium">Longitud del Contenido (para IA)</Label>
              <Select value={aiContentLength} onValueChange={setAiContentLength}>
                  <SelectTrigger id="aiContentLength">
                      <SelectValue placeholder="Selecciona longitud del contenido" />
                  </SelectTrigger>
                  <SelectContent>
                      <SelectItem value="short">Corto</SelectItem>
                      <SelectItem value="medium">Medio</SelectItem>
                      <SelectItem value="long">Largo</SelectItem>
                  </SelectContent>
              </Select>
            </div>
            <div>
              <Label htmlFor="aiBrandingElements" className="text-sm font-medium">Elementos de Marca (para IA)</Label>
              <Input 
                id="aiBrandingElements"
                placeholder="ej., 'Logo arriba-izquierda'" 
                value={aiBrandingElements}
                onChange={(e) => setAiBrandingElements(e.target.value)}
              />
            </div>
            <Button
              type="button"
              onClick={handleSuggestLayout}
              className="w-full"
              disabled={isPendingAI || isPendingSubmit || isGeneratingPdf}
            >
              {isPendingAI ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : (
                <Sparkles className="mr-2 h-4 w-4" />
              )}
              Obtener Sugerencia de Diseño PDF
            </Button>
            {aiSuggestion && (
              <Alert className="mt-6 bg-primary/5 border-primary/20">
                <Sparkles className="h-5 w-5 text-primary" />
                <AlertTitle className="font-semibold text-primary">Sugerencia de Diseño</AlertTitle>
                <AlertDescription className="mt-2 space-y-2 text-foreground/80">
                  <p><strong>Sugerencia:</strong> {aiSuggestion.layoutSuggestion}</p>
                  <p><strong>Razonamiento:</strong> {aiSuggestion.reasoning}</p>
                </AlertDescription>
              </Alert>
            )}
             {!aiSuggestion && !isPendingAI && (
                <div className="mt-6 p-4 text-center text-muted-foreground">
                  <Image src="https://placehold.co/300x200.png" alt="Marcador de posición para sugerencia de IA" width={300} height={200} className="mx-auto rounded-md mb-2" data-ai-hint="abstract idea" />
                  Completa los campos de arriba y haz clic en el botón para obtener ideas de diseño PDF impulsadas por IA.
                </div>
            )}
            {isPendingAI && (
                <div className="mt-6 p-4 text-center text-muted-foreground flex flex-col items-center">
                    <Loader2 className="h-8 w-8 animate-spin text-primary mb-2" />
                    <p>Generando sugerencia de IA...</p>
                </div>
            )}
          </CardContent>
        </Card>
         <Card className="shadow-md bg-accent/10 border-accent/20">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-xl text-accent">
              <Settings2 className="h-6 w-6" />
              Almacenamiento (Próximamente)
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            <p className="text-sm text-muted-foreground">Funciones para guardar en Google Drive o almacenamiento interno se añadirán aquí.</p>
             <Button variant="outline" className="w-full" disabled>
              Guardar en Google Drive (Próximamente)
            </Button>
            <Button variant="outline" className="w-full" disabled>
              Guardar en Almacenamiento Interno (Próximamente)
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
